	},
	//页面加载生命周期
	onLoad:function(){
		uni.showLoading({
			title:"加载中...."
		});
		uni.request({//发送页面请求
		   url:'https://unidemo.dcloud.net.cn/api/news',//后台端口
		   method:'GET',//接收后台传来的信息
		   data:{},
		   success:res =>{
			   console.log(res);
			   //当前数组放在当前页面
			   this.news = res.data;//把数据放在页面上
			   uni.hideLoading();//加载完毕隐藏
		   }
			
		})
	},
	methods:{
